@keyframes line-anim {
  to {
    stroke-dashoffset: 0;
  }
}
@keyframes fill {
  0% {
    fill: transparent;
  }
  50% {
    fill: #01152b;
  }
  75% {
    fill: #01152b;
  }
  100% {
    opacity: 0%;
    fill: #01152b;
  }
}

@keyframes logofade {
  to {
    opacity: 0%;
  }
}
.namediv {
  position: absolute;
  inset: 0;
  background-color: #01152b;
  opacity: 100%;
  animation: logofade 6s ease forwards 5s;
  overflow: x;
  pointer-events: none;
  z-index: 100;
}
body {
  font-family: "Work Sans", sans-serif;
}
html {
  scroll-behavior: smooth;
}
.home {
  margin: auto;
  width: 70%;
  display: flex;
  flex-direction: column;
}
@media only screen and (max-width: 1024px) {
  .home {
    width: 85%;
  }
}

@media only screen and (max-width: 512px) {
  .home {
    width: 100%;
  }
}
