.emailright {
    bottom: 50%;
    cursor: pointer;
    font-family: 'Courier New', Courier, monospace;
    right: 110px;
    width: 20px;
    height: 200px;
    position: fixed;
    color: #64ffda;
    font-size: 19px;
    line-height: 1.2;
    white-space: nowrap;
    transform: rotate( -270deg);
}
