.stick_follow_icon {
    top: 50%;
    left: 30px;
    width: 20px;
    height: 200px;
    position: fixed;
    margin-top: -100px;
}

.stick_follow_icon ul {
    list-style: none;
    padding: 0;
    margin: 0;
}

.stick_follow_icon p {
    top: 70px;
    left: -7px;
    width: 68px;
    height: 20px;
    color: #64ffda;
    font-size: 12px;
    font-weight: 600;
    line-height: 1.2;
    white-space: nowrap;
    position: relative;
    transform: rotate( -90deg);
}

.stick_follow_icon ul li {
    display: block;
    font-size: 12px;
    text-align: center;
    margin-bottom: 10px;
    transition: all .3s;
}

.stick_follow_icon p:after {
    top: 9px;
    right: -48px;
    width: 40px;
    height: 1px;
    content: "";
    display: block;
    position: absolute;
    background-color: var(--text-color);
}

.imagelineL{
    left: 24px;
    width: 50px;
    margin: 0 auto;
    position: relative;
}

@media only screen and (max-width: 1026px) {
    .stick_follow_icon {
        display: none;
    }
    .emailright{
        display: none;
    }
}